@use "sass:math";@use "sass:string";@use "sass:color";@use "sass:map";@import "_colors"; @import "_variables"; @import "_breakpoints"; @import "_mixins"; .wc-block-components-totals-promotion__form {
  margin-bottom: 0;

  .components-combobox-control__suggestions-container {
    border: 0;
    padding: 0;
    position: relative;
  }

  .components-flex {
    height: auto;
    position: relative;
    padding: 0;
  }

  .components-flex-item:not(.components-flex-block) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    margin-right: 0.5rem;

    button {
      background: transparent;
    }
  }

  input.components-combobox-control__input[type=text] {
    background-color: #fff;
    border: 1px solid hsla(0, 0%, 7%, .8);
    border-radius: 4px;
    box-sizing: border-box;
    color: #2b2d2f;
    font-family: inherit;
    font-size: 1rem;
    line-height: 1em;
    margin: 0;
    padding: 1em;
    width: 100%;

    &.is-active {
      padding: 1.5em 0.5em 0.5em;
    }
  }

  .components-base-control__field {
    position: relative;
  }

  .components-base-control__label {
    color: hsla(0, 0%, 7%, .5);
    cursor: text;
    font-family: inherit;
    font-size: 1rem;
    font-style: inherit;
    font-weight: inherit;
    left: 0.5625em;
    letter-spacing: inherit;
    line-height: 1.25;
    margin: 0;
    max-width: calc(100% - 32px);
    overflow: hidden;
    position: absolute;
    text-decoration: inherit;
    text-overflow: ellipsis;
    text-transform: inherit;
    top: 0;
    transform: translateY(1em);
    transform-origin: top left;
    transition: all .2s ease;
    z-index: 1;

    &.is-active {
      transform: translateY(0.5em) scale(.875);
    }
  }

  $item-height: 2rem;

  .components-form-token-field__suggestions-list {
    position: absolute;
    padding: 0;
    margin: 0;
    top: 3rem;
    background: initial;
    background-color: white;
    list-style: none;
    border: 1px solid black;
    z-index: 100;
    width: 100%;
    overflow: scroll;
    overflow-x: hidden;
    max-height: calc(4 * $item-height);
    margin-top:2px;

    li {
      height: $item-height;
      padding: 0.5rem;
      font-size: 1rem;
      cursor: pointer;

      &:hover {
        background-color: $gray-900;
        font-weight: bold;
      }
    }
  }


}

.wc-block-components-totals-promotion__button {
  width: 100%;
}

.wc-block-components-totals-promotion__available-list {
  .components-panel__body-toggle {
    background-color: initial;
    padding: 0;
    font-size: 1rem;
    width: 100%;

    .components-panel__icon {
      display: none;
    }

    .components-panel__arrow {
      vertical-align: top;
    }
  }
}
